import React, { useContext } from "react"
import { Link } from "react-router-dom"
import ScrollUpButton from "react-scroll-up-button"
// import { ScrollTo } from "react-scroll-to";
// import { FaChevronCircleUp } from "react-icons/fa";
// import {CircleArrow as ScrollUpButton} from "react-scroll-up-button"
// import{ScrollToTop} from 'react-scroll-up'

import Card from "./Card/Card"
import classes from "./Cards.module.css"
import FoodContext from "../../context/FoodContext/FoodContext"
import ThemeContext from "../../context/ThemeContext/ThemeContext"
import {Button} from '@material-ui/core';

function Cards() {
  const context = useContext(FoodContext)
  const theme = useContext(ThemeContext)

  let loader
  if (theme.theme === "light") {
    loader = "#212832"
  } else {
    loader = "#FFF"
  }

  let foods

  if (context.meals === null || context.meals === undefined) {
    foods = <p className="text-copy-primary sm:mx-auto">No result to display </p>
  } else {
    foods = context.meals.map((food) => {
      let tags = ["Details"]
      if (food.strTags !== undefined && food.strTags !== null) {
        if (food.strTags.includes(",")) {
          tags = food.strTags.split(",")
        } else {
          tags = [food.strTags]
        }
      }

      return (
        <Link key={food.idMeal} to={`/food/${food.idMeal}`}>
          <Card
            title={food.strMeal}
            category={food.strCategory}
            area={food.strArea}
            thumb={food.strMealThumb}
            tags={tags}
          />
        </Link>
      )
    })
  }

  if (context.isLoading === true) {
    foods = (
      <div
        style={{ color: loader }}
        className={[classes.LaSquareSpin, classes.La2x, "mx-auto"].join(" ")}
      >
        <div></div>
      </div>
    )
  }

  return <div className={[classes.Cards, "sm:gap-10"].join(" ")}>{foods}  
             <ScrollUpButton
              style={{
                backgroundColor: "red",
                width: "30px",
                height: "30px",
              }}              
            />  

            {/* <ScrollToTop showUnder={160}>
              <span>UP</span>
            </ScrollToTop>
             
       {/* <ScrollTo >
        {({ scroll }) => (
          <a onClick={() => scroll({ x: 20, y: 500 })} style={{color:"red", fontWeight:"bold",fontSize: "30px", display:"flex", justifyContent:"center", position:"relative", top:"-20%"}}><FaChevronCircleUp /></a>
        )}
      </ScrollTo>  */}
            </div>

            
  
  

}

export default Cards
